import { PageHeader } from "../../../../shared/PageHeader";
import { PageFooter } from "../../../../shared/PageFooter";
import { useTranslation } from "react-i18next";
import { Button } from "primereact/button";
import { Link } from "react-router-dom";
import { ListOfRequestsTable } from "../../../../features/table";
import { Toast } from "primereact/toast";
import { useRef } from "react";
import "./style.css";
import { initialProjectDirectory } from "../../../../entities/initialProjectDirectory";


const ListOfRequests = ({currentUser}) => {
  const { i18n } = useTranslation();
  const { t } = i18n;

  const toastRef = useRef(null);

  return (
    <>
      <Toast ref={toastRef} />

      <main className="page ListOfRequests_page container_local">
        <PageHeader title={t("menu.subMenuVacations.menuItemRequestsList")}>
          <Link to={`${initialProjectDirectory}/vacations/createRequest`}>
            <Button
              label={`+ ${t(
                "pages.vacations.listOfRequests.addNewRequestButton"
              )}`}
              outlined
            />
          </Link>
        </PageHeader>

        <div className="page_content">
          <ListOfRequestsTable i18n={i18n} toastRef={toastRef} currentUser={currentUser}/>
        </div>

        <PageFooter />
      </main>
    </>
  );
};

export default ListOfRequests;
